import React from 'react'
import styled from 'styled-components';

const PageMessage = styled.h2`
    font-size: 25pt;
    color: #b7b7b7;
    font-family: "Crimson Text", georgia, sans-serif;
    font-weight: normal;
    margin-bottom: 40px;
    padding-bottom: 10px;
    text-align: center;
    margin-top: 150px;
    border-bottom: none;
`

export default PageMessage


